<template>
  <transition enter-active-class="animated lightSpeedIn" leave-active-class="animated lightSpeedOut" mode="out-in">
    <li class="list-group-item" :class="{finish: todo.isCompleted, edit: todo == editedTodo, hover: isHover}" @dblclick="editTodo(todo)" @mouseenter="isHover = true" @mouseleave="isHover = false">
      <div class="view">
        <span class="checkbox" :class="{on: todo.isCompleted}" @click="complete(todo)"></span>
        <div class="list-inner">
          {{ todo.text }}
        </div>
        <!-- <transition enter-active-class="animated fadeIn"> -->
          <div class="opt-group">
              <span class="iconfont icon-edit" @click="editTodo(todo)"></span>
              <span class="iconfont icon-wancheng" @click="complete(todo)"></span>
              <span class="iconfont icon-del" @click="remove(todo)"></span>
          </div>
        <!-- </transition> -->
      </div>
      <input type="text" class="editInput" @blur="doneEdit($event, todo)" @keyup.enter="doneEdit($event, todo)" @keyup.esc="cancelEdit(todo)" v-focus :value="todo.text">
    </li>
  </transition>
</template>
<script>
  export default {
    props: {
      todo: {
        type: Object,
        require: true
      }
    },
    data: function() {
      return {
        isHover: false,
        isEdit: false,
        editedTodo: null
      }
    },
    directives: {
      'focus': function(el, binding) {
        el.focus()
      }
    },
    methods: {
      edit(todo) {
        this.$store.dispatch('editTodo', todo);
      },
      complete(todo) {
        this.$store.dispatch('completeTodo', todo);
      },
      remove(todo) {
        this.$store.dispatch('removeTodo', todo);
      },
      completeAll() {
        this.$store.dispatch('completeAll');
      },
      editTodo(todo) {
        this.editedTodo = todo
      },
      cancelEdit() {
        this.editedTodo = null;
      },
      doneEdit(e, todo) {
        this.editedTodo = null;
        if (todo.text == e.target.value.trim()) {
          return;
        }
        todo.text = e.target.value.trim();
        // this.editedTodo.isCompleted = todo.isCompleted;
        this.$store.dispatch('editTodoItem', todo);
      }
    }
  }
</script>
<style lang="scss">
  .list-group-item {
    line-height: 30px;
    position: relative;
    padding: 10px 15px;
    border-top: 1px solid #ededed;
    border-bottom: 1px solid #ededed;
    margin-top: -1px;
    .opt-group {
      // display: none;
      font-size: 0;
    }
    &:hover {
      .icon-edit {
        color: #718aea;
      }
      .icon-del {
        color: #f57055;
      }
      .icon-wancheng {
        color: #4cc483;
      }
    }
    .iconfont {
      cursor: pointer;
      font-size: 20px;
      vertical-align: middle;
      margin-left: 10px;
      color: #b9c3cd;
    }
  }

</style>
